.flex {
    display: flex;

    &-inline {
        display: inline-flex;
    }
    &-wrap {
        flex-wrap: wrap;
    }
    &-col {
        flex-direction: column;
    }
    &-col-reverse {
        flex-direction: column-reverse;
    }
    &-row {
        flex-direction: row;
    }
    &-1 {
        flex: 1;
    }
    &-0 {
        flex: 0;
    }
    &-cc {
        justify-content: center;
        align-items: center;
    }
}

.content {
    &-start {
        justify-content: flex-start;
    }
    &-center {
        justify-content: center;
    }
    &-end {
        justify-content: flex-end;
    }
    &-space-b {
        justify-content: space-between;
    }
}

.items {
    &-start {
        align-items: flex-start;
    }
    &-center {
        align-items: center;
    }
    &-end {
        align-items: flex-end;
    }
}

.self {
    &-justify-start {
        justify-self: flex-start;
    }
    &-justify-center {
        justify-self: center;
    }
    &-justify-end {
        justify-self: flex-end;
    }
}

.self {
    &-align-start {
        align-self: flex-start;
    }
    &-align-center {
        align-self: center;
    }
    &-align-end {
        align-self: flex-end;
    }
}

.gap {
    gap: var(--gap);
    grid-gap: var(--gap);
}
.gap {
    &-xs {
        gap: var(--gap-xs);
        grid-gap: var(--gap-xs);
    }
    &-sm {
        gap: var(--gap-sm);
        grid-gap: var(--gap-sm);
    }
    &-md {
        gap: 0.75em;
        grid-gap: 0.75em;
    }
    &-lg {
        gap: var(--gap-lg);
        grid-gap: var(--gap-lg);
    }
    &-xl {
        gap: var(--gap-xl);
        grid-gap: var(--gap-xl);
    }
}
